import React from 'react'
import PropTypes from 'prop-types'
import {get} from 'lodash';
import MainActions from "./MainActions";
import PlaceInfo from "./PlaceInfo";
import DateInfo from "./dateInfo/DateInfo";
import MainInfo from "./mainInfo/MainInfo";

require('./style.scss')

export default class Event extends React.Component {
	constructor(props) {
		super(props)
	}
	render() {
		const title = get(this.props.event, 'title')
    const description = get(this.props.event, 'description')
    const width = get(this.props.event, 'imageSize.width')
    const height = get(this.props.event, 'imageSize.height')
		const imageSrc = `http://via.placeholder.com/${width}x${height}`
		return (
			<div className="event">
        <PlaceInfo/>
        <img className="event__cover" src={imageSrc}/>
				<div className="event__caption">
          <div className="event__heart">
            <i className="fa fa-heart"/>
          </div>
          <h1 className="event__title">{title}</h1>
				</div>
        <div className="event__infoCaption">
          <div className="event__date">
            <DateInfo/>
          </div>
          <div className="event__info">
            <MainInfo/>
          </div>
        </div>
        <MainActions/>
			</div>
		)
	}
}

Event.propTypes={
  event: PropTypes.object,
}


